.about-main {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    background-color: #ff7f50;
    gap: 20px;
    width: 99%;
    margin: 10px;
    padding: 40px 20px;
    box-sizing: border-box;
    border-radius: 8px;
}

.about-header {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 85%;
    background-color: #ff956e;
    padding: 1rem;
    border-radius: 5px;
    font-family: "Barrio", system-ui;
    font-size: 1.5rem;
}

.about-content {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
    padding: 2rem;
}

.about-left {
    max-width: 25%;
    flex: 1;
    padding: 10px;
    margin-right: 20px;
}

.about-left img {
    width: 100%;
    height: auto;
    border-radius: 8px;
}

.about-right {
    flex: 2;
    padding: 10px;
    max-width: 50%;
    background-color: #ff956e;
    border: 0px;
    border-radius: 5px;
}