.navbar {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    background-color: #ee55cc;
    width: 99%;
    margin: 10px;
    padding: 20px;
    box-sizing: border-box;
    border-radius: 8px;
}

.navbar-left .logo {
    font-size: 2.5rem;
    font-weight: bold;
    color: #1a1a1a;
    text-decoration: none;
    font-family: "Barrio", system-ui;
}

.navbar-right .nav-links {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    list-style-type: none;
    display: flex;
    margin: 0;
    padding: 10px;
}

.navbar-right .nav-links li {
    margin-right: 1rem;
}

.navbar-right .nav-links a {
    color: pink;
    text-decoration: none;
}

.nav-links a:hover {
    color: red;
}
