.projects-main {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: #800080;
    gap: 20px;
    width: 99%;
    padding: 40px 20px;
    box-sizing: border-box;
    border-radius: 8px;

    flex-wrap: wrap;
}

.projects-header {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 85%;
    background-color: #9c009c;
    padding: 1rem;
    border-radius: 5px;
    font-family: "Barrio", system-ui;
    font-size: 1.5rem;
}

.projects-content {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
    padding: 2rem;
    gap: 20px;
}

.individual-projects {
    display: flex;
    flex-direction: column;
    align-items: stretch;
    gap: 40px;
    width: 100%;
    padding: 2rem 0;
}

/* Each project block */
.project-section {
    display: flex;
    flex-direction: row;
    background-color: #9c009c;
    padding: 2rem;
    border-radius: 8px;
    width: 90%;
    gap: 2rem;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
}

/* Alternate layout for variety */
.project-section:nth-child(even) {
  flex-direction: row-reverse;
  background-color: #6a006a;
}

/* Image styling */
.project-image img {
  max-width: 800px;
  max-height: 450px;
  border-radius: 8px;
}

/* Text content */
.project-info {
  width: 300px;
  max-height: 450px;
  color: white;
}

.project-info h2 {
  font-family: "Barrio", system-ui;
  font-size: 1.8rem;
  margin-bottom: 1rem;
}

.project-info a {
  display: inline-block;
  margin-top: 1rem;
  text-decoration: none;
  color: #ffddee;
  font-weight: bold;
}

.project-info p {
    color: black;
}